.App {
  text-align: center;
  background-color: rgb(255, 255, 255);
  width: 100%;
  /* display: grid;
  grid-template-rows: 10vh 90vh; */
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.header {
  background-color: rgb(251,251,251);
  height: 10vh;
  box-shadow: 0px 0px 3px 3px rgb(226, 226, 226);
  display: flex;
  overflow: hidden;
  position: fixed;
  width: 100vw;
  z-index: 3;
}

.header div {
  align-self: center;
  transition: transform 300ms;
  transform-origin: center;
}

.header div:hover {
  cursor: pointer;
}

.logo {
  margin-left: 40px;
  margin-right: auto;
  margin-top: 10px;
}

.logo img {
  height: 100%;
  pointer-events: none;
}

.new-recipe {
  color: rgb(148, 148, 148);
}

.new-recipe:hover {
  color: #C70017;
}

.new-recipe-icon-container {
  margin-right: 40px;
  width: 35px;
  height: 32px;
}

.new-recipe-icon-container i{
  position: absolute;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.fa-plus-circle {
  color: rgb(251,251,251);
}

.home {
  margin-right: 40px;
  color: rgb(148, 148, 148);
}

.home:hover {
  color: #C70017;
}

.log-in-out {
  display: flex;
  margin-right: 40px;
}

.current-display {
  margin-top: 10vh;
  width: 100%;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
}

/* -------------------- SIGNED-IN-USER -------------------- */

.sign-in {
  margin-right: 40px;
  color: rgb(148, 148, 148);
}

.sign-in:hover {
  color: #C70017;
}

.signed-in {
  margin-right: 40px;
}

.user-name {
  margin: auto;
}

.user-pic {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  right: 15px;
}

.home-view {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/* -------------------- ADD NEW RECIPE -------------------- */

.new-recipe-view {
  height: 90vh;
  display: grid;
  grid-template-columns: 1fr 3fr;
}

.recipe-form {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  box-shadow: 0px 0px 2px 2px rgb(200, 200, 200);
  display: grid;
  grid-template-rows: 1fr 6fr 1fr;
  margin: auto;
  background-color: rgb(251,251,251);
}

.recipe-form h2 {
  font-style: italic;
}

.recipe-creation-header {
  grid-row-start: 1;
  grid-row-end: 2;
  color: #C70017;
  margin: auto;
}

.form-title {
  margin: auto;
  grid-row-start: 2;
  grid-row-end: 3;
  display: grid;
  grid-template-rows: 1fr 1fr 5fr;
  font-size: 18px;
}

.form-title input {
  margin: auto;
  all: unset;
  border-bottom: 2px solid rgba(0, 0, 0, 0.623);
  height: 30px;
}

.form-title input:focus {
  border-bottom: 2px solid green;
}

.form-photo {
  margin: auto;
  grid-row-start: 2;
  grid-row-end: 3;
  display: grid;
  grid-template-rows: 1fr 1fr 5fr;
  font-size: 18px;
}

.form-photo input {
  all: unset;
  width: 80%;
  margin-right: auto;
  margin-left: auto;
}

.form-description {
  margin: auto;
  grid-row-start: 2;
  grid-row-end: 3;
  /* height: 70%; */
  display: grid;
  grid-template-rows: 1fr 1fr 5fr;
  font-size: 18px;
}

.form-description input {
  all: unset;
  width: 80%;
  margin-right: auto;
  margin-left: auto;
}

.form-button-container {
  grid-row-start: 3;
  grid-row-end: 4;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
}

.previous-step-button {
  grid-column-start: 1;
  grid-column-end: 2;
  display: flex;
}

.previous-step-button i {
  margin: auto;
}

.previous-step-button i:hover {
  cursor: pointer;
  color: rgb(17, 187, 40);
  transform: scale(1.1);
}

.next-step-button {
  grid-column-start: 3;
  grid-column-end: 4;
  display: flex;
}

.next-step-button i {
  margin: auto;
}

.next-step-button i:hover {
  cursor: pointer;
  color: rgb(17, 187, 40);
  transform: scale(1.1);
}

.submit-button {
  align-self: center;
  margin: auto;
  width: 100px;
  grid-row-start: 2;
  grid-row-end: 3;
}

.draft-recipe-card-container {
  grid-column-start: 2;
  grid-column-end: 3;
  display: flex;
  margin: auto;
}

/* -------------------- RECIPE CARDS -------------------- */

.recipe-card {
  background-color: transparent;
  width: 276px;
  height: 336px;
  perspective: 1000px;
  margin: 20px;
}


/* This container is needed to position the front and back side */
.recipe-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
  box-shadow: 0px 0px 13px 0px #8a8a8a;
}

/* Do an horizontal flip when you move the mouse over the flip box container */
.recipe-card:hover .recipe-card-inner {
  transform: rotateY(180deg);
}

/* Position the front and back side */
.recipe-card-front, .recipe-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

/* Style the front side (fallback if image is missing) */
.recipe-card-front {
  background-color: #ffffff;
  background-image: url("https://www.transparenttextures.com/patterns/cream-pixels.png");
  color: black;
  display: grid;
  grid-template-rows: 277px 59px;
  text-align: center;
  border-radius: 2px;
}

.image {
  margin-top: 18px;
  justify-self: center;
  width: 246px;
  height: 240px;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.image-container {
  margin-top: 18px;
  justify-self: center;
  width: 246px;
  height: 240px;
  background-color: transparent;
  /* display: block; */
  position: relative;
  overflow: hidden;
}

.image-container img {
  position: absolute;
  top: -15%;
  left: 0;
  width: 100%;
  /* transform: translate3d(0,0,0); */
  -webkit-backface-visibility: hidden; /*Safari */
  backface-visibility: hidden;
}

