  margin-top: 18px;
  justify-self: center;
  width: 308px;
  height: 300px;
  width: 246px;
  height: 240px;
  object-fit: cover;
} */

.recipe-front-text {
  grid-row-start: 2;
  grid-row-end: 3;
  align-self: end;
  font-size: 10px;
  margin-bottom: 9px;
}

.recipe-card-back {
  background-color: transparent;
  color: black;
  height: 336px;
  width: 276px;
  transform: rotateY(180deg);
  text-align: center;
  border-radius: 2px;
  display: grid;
  grid-template-rows: 1fr 1fr 4fr;
  overflow: hidden;
}


.recipe-back-title {
  grid-row-start: 1;
  grid-row-end: 2;
  font-size: 10px;
  overflow: hidden;
}

.recipe-back-description {
  grid-row-start: 3;
  grid-row-end: 4;
  font-size: 14px;
  width: 90%;
  margin: 0px auto;
  text-align: center;
  overflow: hidden;
}

.recipe-back-author-container {
  grid-row-start: 2;
  grid-row-end: 3;
  display: grid;
  grid-template-rows: 1fr 1fr;
  position: relative;
}

.recipe-back-author {
  grid-row-start: 2;
  grid-row-end: 3;
  font-size: 12px;
}

.recipe-back-author-pic img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background-color: grey;
  grid-row-start: 1;
  grid-row-end: 2;
  position: absolute;
  left: 50%;
  bottom: 50%;
  transform: translate(-50%, 50%);
  transition: all 300ms;
  transform-origin: center;
}

.recipe-back-author-pic img:hover {
  width: 65px;
  height: 65px;
}

/* -------------- DEMO CARDS FOR CARD CREATION -------------- */

.polaroid-development {
  margin-top: 19px;
  justify-self: center;
  width: 300px;
  height: 308px;
  opacity: 1;
  position: absolute;
  background-color: black;
  z-index: 2;
  transition: all 2000ms;
}

.develop {
  opacity: 0;
  background-color: white;
}

